.Drawer {
    padding: 20px 10px;
    width: 80%;
    max-width: 300px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;

    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 90;

    background: #fff;
    -webkit-transform: translateX(0px);
        -ms-transform: translateX(0px);
            transform: translateX(0px);

    -webkit-transition: -webkit-transform .22s ease-in;

    transition: -webkit-transform .22s ease-in;

    -o-transition: transform .22s ease-in;

    transition: transform .22s ease-in;

    transition: transform .22s ease-in, -webkit-transform .22s ease-in;
}

.Drawer.close {
    -webkit-transform: translateX(-300px);
        -ms-transform: translateX(-300px);
            transform: translateX(-300px);
}

.Drawer ul {
    margin: 0;
    padding: 0;

    list-style: none;
}

.Drawer ul li {
    margin-bottom: 15px;
}

.Drawer ul li a {
    padding: 0 20px 10px 20px;
    position: relative;

    font-size: 30px;
    color: #363d54;
    text-decoration: none;
    background-color: #fff;

    -webkit-transition: opacity .3s;

    -o-transition: opacity .3s;

    transition: opacity .3s;
}

.Drawer ul li a:hover, .active {
    opacity: .7;
}


